.App_wrapper{
    width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-areas:
            "h h"
            "n c"
            "f f";
    grid-template-columns: 250px 1fr;
    grid-template-rows: 260px 1fr 100px;

}

.Main_wrapper{
    background-color: #000;
    grid-area: c;
    color: #ffffff;
}